    --bg-main: #2b2b2b;
    --bg-header: #1f1f1f;
    --bg-card: rgba(127, 255, 212, 0.55);
    --bg-card-unread: rgba(255, 120, 160, 0.75);

    --accent-pink: #ff8fd8;
    --accent-pink-dark: #ff6fcf;

    --text-main: #f5f5f5;
    --text-muted: #d0d0d0;

    --radius-soft: 14px;
    --radius-hard: 28px;

    --shadow-soft: 0 6px 16px rgba(0, 0, 0, 0.25);
    --shadow-hover: 0 12px 24px rgba(0, 0, 0, 0.35);

    --card-aqua: rgba(127, 255, 212, 0.55);
    --card-pink: rgba(255, 120, 160, 0.75);
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Winky Sans", sans-serif;
}

body {
    min-height: 100vh;
    background-color: var(--bg-main);
    color: var(--text-main);
    display: flex;
    flex-direction: column;
}

/* ================= HEADER ================= */

.header_banner {
    background-color: var(--bg-header);
    padding: 1rem;
    box-shadow: 0 3px 10px rgba(255, 143, 216, 0.4);
}

.header_title {
    font-size: clamp(2.2rem, 4vw, 3.5rem);
    text-align: center;
    letter-spacing: 2px;
}

/* ================= CARD GRID ================= */

.card_container {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
    gap: 1.5rem;
    padding: 1.5rem;

    transition: transform 0.25s ease, box-shadow 0.25s ease,
        background-color 0.3s ease;
}

/* ================= CARD ================= */

.card {
    background-color: var(--card-aqua);
    border-radius: var(--radius-soft) var(--radius-hard);
    padding: 1rem 1.2rem;
    box-shadow: var(--shadow-soft);
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
    transition: transform 0.25s ease, box-shadow 0.25s ease;
}

.card:hover {
    transform: translateY(-4px);
    box-shadow: var(--shadow-hover);
}

.title {
    font-size: 1.3rem;
    font-weight: 700;
}

.author {
    font-size: 1.1rem;
    color: #222;
}

.pages,
.read_status {
    font-size: 1rem;
    color: #222;
}

/* ================= CARD BUTTONS ================= */

.buttons_div {
    display: flex;
    gap: 0.5rem;
    margin-top: auto;
}

.card_buttons {
    flex: 1;
    border: none;
    border-radius: 10px;
    padding: 0.4rem;
    font-size: 0.95rem;
    background-color: rgba(255, 255, 255, 0.7);
    cursor: pointer;
    transition: background-color 0.2s ease, transform 0.2s ease;
}

.card_buttons:hover {
    background-color: white;
    transform: scale(1.05);
}

/* ================= ADD BOOK BUTTON ================= */

.btn_add_book {
    position: fixed;
    bottom: 20px;
    right: 20px;

    font-size: 1.2rem;
    padding: 0.7rem 1.1rem;
    border-radius: 999px;
    border: none;
    background-color: var(--accent-pink);
    color: #000;
    box-shadow: var(--shadow-soft);
    cursor: pointer;
    transition: transform 0.2s ease, box-shadow 0.2s ease;
}

.btn_add_book:hover {
    transform: scale(1.1);
    box-shadow: var(--shadow-hover);
}

/* ================= DIALOG ================= */

.new_book_dialog {
    border: none;
    border-radius: var(--radius-soft) var(--radius-hard);
    background: transparent;
    margin: auto;
    padding: 0;
}

.new_book_dialog::backdrop {
    background: rgba(0, 0, 0, 0.6);
}

/* ================= FORM ================= */

.add_book_container {
    background-color: var(--bg-header);
    padding: 1.5rem 2rem;
    border-radius: var(--radius-soft) var(--radius-hard);
    min-width: clamp(260px, 60vw, 520px);
    box-shadow: var(--shadow-hover);
}

.form_heading {
    font-size: 2.2rem;
    margin-bottom: 1rem;
    text-align: center;
}

form {
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.form_line {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 1rem;
}

.form_line input[type="checkbox"] {
    flex: 0 0 auto;
    margin-left: auto;
}

label {
    font-size: 1.1rem;
    color: var(--text-muted);
}

input {
    flex: 1;
    height: 2.2rem;
    padding: 0 0.5rem;
    border-radius: 8px;
    border: none;
    font-size: 1rem;
}

input[type="number"] {
    max-width: 100px;
}

input[type="checkbox"] {
    width: 1.6rem;
    height: 1.6rem;
    cursor: pointer;
}

/* ================= FORM BUTTONS ================= */

.btn_submit,
.btn_close {
    margin-top: 0.5rem;
    padding: 0.6rem;
    font-size: 1.2rem;
    border-radius: 10px;
    border: none;
    background-color: var(--accent-pink);
    cursor: pointer;
    transition: background-color 0.2s ease, transform 0.2s ease;
}

.btn_close {
    background-color: #ccc;
}

.btn_submit:hover {
    background-color: var(--accent-pink-dark);
    transform: scale(1.05);
}

.btn_close:hover {
    background-color: #eee;
    transform: scale(1.05);
}

/* ================= MOBILE ================= */

@media (max-width: 360px) {
    .card_container {
        grid-template-columns: 1fr;
    }
}
